  },
  methods: {
    onSubmit() {
      console.log("submit!");
    }
  },
  components: {}
};
</script>

<style scoped lang="scss">
.login {
  width: 380px;
  height: 506px;
  background: #fff;
  margin: auto;
  margin-bottom: 30px;
  padding: 50px;
  .login-top {
    width: 100%;
    height: 110px;
    text-align: center;
    position: relative;
    h2{
      margin-bottom: 15px;
    }
  }
  button{
    height: 46px;
    width:100%;
    background: #4786ff;
    font-size: 16px;
    border: 0;
    color: #fff;
    display: inline-block;
    cursor: pointer;
    box-shadow: 0 0 10px 1px #b5ceff;
    border-radius: 2px;
  }
  .login-in{
    margin: 10px 0;
    span:last-child{
